@use '@angular/material' as mat;
@include mat.core();

// Define light theme.
$light-primary: mat.define-palette(mat.$light-blue-palette);
$light-accent: mat.define-palette(mat.$pink-palette, 500, 900, A100);
$light-warn: mat.define-palette(mat.$deep-orange-palette);
$light-theme: mat.define-light-theme((
  color: (
    primary: $light-primary,
    accent: $light-accent,
    warn: $light-warn
  )
));
// Apply the light theme by default
@include mat.all-component-themes($light-theme);

// Define dark theme.
$dark-primary: mat.define-palette(mat.$blue-grey-palette, A200);
$dark-accent: mat.define-palette(mat.$amber-palette, A200, A100, A400);
$dark-warn: mat.define-palette(mat.$deep-orange-palette);
$dark-theme: mat.define-dark-theme((
 color: (
   primary: $dark-primary,
   accent: $dark-accent,
   warn: $dark-warn,
 )
));

.dark-theme {
  @include mat.all-component-colors($dark-theme);
}

.mat-toolbar {
  background-color: transparent;
}

.mat-card-media {
  color: rgba(0, 0, 0, .54);
}

.mat-card.mat-card-ripple:hover {
  z-index: 1000;
  box-shadow: rgba(0, 0, 0, 0.3) 0 16px 16px 0;
  cursor: pointer;
}

.mat-tab-body-wrapper {
  padding-top: 16px;
}

.mat-dialog-content-full .mat-dialog-container {
  padding: 24px 24px 0 24px;
}

.mat-content {
  align-items: center;
}

// ---------------------
.mat-dialog-actions {
  justify-content: space-between;
}

.mat-dialog-actions-end {
  justify-content: flex-end;
}

.mat-dialog-actions-start {
  justify-content: flex-start;
}