@keyframes pulse {
  0% {
    margin-top: 0;
  }
  60% {
    margin-top: -4px;
  }
  100% {
    margin-top: 0;
  }
}

.map-marker {
  display: flex;
  justify-content: center;
  color: #1e88e5;
}

.map-marker i {
  position: absolute;
  top: -16px;
}

.map-marker-animated i {
  animation: pulse 0.7s infinite;
}

.pulse-animation {
  margin:50px;
  display: block;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background: #ff8717;
  cursor: pointer;  
  animation: pulse 0.5s infinite;
  float:left;
}