// Header Basic
// ------------------
.header-basic {
  display: flex;
  align-items: center;
  position: relative;
  padding: 16px;
  margin: 4px 0 8px 0;

  box-shadow: 0 0px 0px 0px #0003, 0 1px 0px #00000024, 0 1px 3px #0000001f;
  border-radius: 4px;

  .title-stacked {
    display: flex;
    flex-direction: column;
  }

  .title,
  .title-stacked span:nth-child(1) {
    font-size: 1.125rem;
    line-height: 1.4em;
    font-weight: 300;
    font-weight: bold;
  }

  .title-stacked span:nth-child(2) {
    font-size: 0.8rem;
  }
}

// Header Main
// ------------------
.header-main {
  padding: 16px;
  margin: 4px 0 8px 0;

  .title {
    font-weight: bold;
    font-size: larger;
  }
}



// Color
// ------------------
.header-basic {
  background: white;
  color: black;
}

.dark-theme .header-basic {
  background: #424242;
  color: white;
}