body {
  margin: 0;
  font-family: Roboto, sans-serif;
  margin: 0 auto;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

.spacer {
  flex: 1 1 auto;
}

.end {
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;

  button {
    margin-left: 8px;
  }
}

.elevation-4 {
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
  border-radius: 4px;
}

.chip {
  display: inline-block;
  height: 20px;
  width: fit-content;
  font-size: 12px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.6);
  line-height: 20px;
  padding: 0 8px;
  border-radius: 16px;
  background-color: #e4e4e4;
  margin-bottom: 4px;
  margin-right: 4px;
}

.content-center {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}


.fas {
  font-size: 24px;
}