.chart-container {
  display: flex;
  justify-content: center;
}



.chart-tooltip {
  position: absolute;
  background-color: white;
  border: 0;
  border-radius: 4px;
  font-size: 12px;
  padding: 8px;
  z-index: 500;
  color: black;
  opacity: 0;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.chart-line {
  fill: none;
  stroke-opacity: 1.5;
}

.chart-line-focus {
  fill: none;
  stroke: steelblue;
  stroke-opacity: 1.5;
}

.chart-axis path {
  fill: none;
  stroke: #777;
  shape-rendering: crispEdges;
}