.list {
  position: relative;
  display: flex;
  flex-flow: column;
}

.list-item-horizontal {
  display: flex;
  flex-flow: row;
  margin: 8px 0;

  div {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 50%;
    font-size: 14px;
    font-weight: 400;
  }
}

.list-item-vertical {
  display: flex;
  flex-direction: column;
  margin-bottom: 8px;

  div:nth-child(1) {
    opacity: 0.8;
    font-size: 12px;
  }
}