.table-600 {
  display: block;
  max-height: 600px;
  overflow-y: auto;
}

.table-column-end {
  display: flex;
  justify-content: flex-end;
}

.table-column-small {
  flex: 80px;
  max-width: 80px;
}

.table-cell-stacked {
  display: flex;
  flex-flow: column;
  width: 100%;

  div:nth-child(1) {
    margin-bottom: 4px;
  }

  div:nth-child(2) {
    font-size: 12px;
  }
}

// Mat Table with Expansion Panel
// ---------------
/* This will fix the height of the expansion panel */
.table-detail-row {
  min-height: unset;
}

mat-row.table-element-row:not(.table-expanded-row):hover {
  background: whitesmoke;
}

.dark-theme mat-row.table-element-row:not(.table-expanded-row):hover {
  background: #ffffff0a;
}

mat-row.table-element-row:not(.table-expanded-row):active {
  background: #efefef;
}

.table-element-row {
  border-bottom-width: 0;
}

.table-element-detail {
  overflow: hidden;
  display: flex;
  width: 100%;
}

// Mat Table
// ---------------
.mat-table,
.dark-theme .mat-table {
  box-shadow: 0 0px 0px 0px #0003, 0 1px 0px #00000024, 0 1px 3px #0000001f;
  border-radius: 4px;
}

.mat-header-row {
  min-height: 42px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.mat-table mat-row:last-child {
  border-bottom: 0 solid
}

.mat-cell.mat-column-select,
.mat-header-cell.mat-column-select {
  overflow: visible;
}

// Color
// ----------
.table-selection {
  background: #fafafa;
  color: black;
}

.dark-theme .table-selection {
  background: #303030;
  color: white;
}

.mat-row,
.dark-theme .mat-row {
  background: none;
}

.dark-theme .mat-table {
  background: #424242;
  color: white;
}

.mat-paginator,
.dark-theme .mat-paginator {
  background: transparent;
}